import React from "react";
import { makeStyles } from "@material-ui/core";
import { Card, CardActionArea, Typography, CardMedia, CardContent, CardActions, IconButton } from "@material-ui/core";
import { Favorite, AddShoppingCart, Share } from "@material-ui/icons";

export const Certificate = (props) => {
    const useStyle = makeStyles((theme) => ({
        root: {
            position: "relative",
            maxWidth: 345,
            margin: theme.spacing(2),
        },
        media: {
            height: 168,
            [theme.breakpoints.down('md')] :{
                height:120
            }
        },
        title: {
            marginTop: theme.spacing(6),
            [theme.breakpoints.down('md')] :{
                marginTop: theme.spacing(3)
            },
        },
    }));
    const classes = useStyle();
    return (
        <Card className={classes.root}>
            <div>
                <CardActionArea>
                    <CardMedia
                        className={classes.media}
                        image="https://akm-img-a-in.tosshub.com/indiatoday/images/story/201811/online-3412473_1920_1.jpeg?tz.RfsTe_UTLHiDqxmpG7PY_nTIBjwF7"
                        src="https://akm-img-a-in.tosshub.com/indiatoday/images/story/201811/online-3412473_1920_1.jpeg?tz.RfsTe_UTLHiDqxmpG7PY_nTIBjwF7"
                    />
                    <CardContent className={classes.title}>
                        <Typography variant="h5" component="h4">
                            Using database with python
                        </Typography>
                        <Typography variant="body2" component="p">
                            University of Michigan
                        </Typography>
                    </CardContent>
                </CardActionArea>
                <CardActions disableSpacing className={classes.action}>
                    <IconButton>
                        <Favorite />
                    </IconButton>
                    <IconButton>
                        <Share />
                    </IconButton>
                    <IconButton style={{ marginLeft: "auto" }}>
                        <AddShoppingCart />
                    </IconButton>
                </CardActions>
            </div>
        </Card>
        // <div className="Certif">
        //     <div className="Certif-img">
        //         <span></span>
        //     </div>
        //     <div className="Certif-backgr"></div>
        //     <span className="Uni-icon"></span>
        //     <div className="title">
        //         <h4>Using Databases with Python</h4>
        //         <div className="Uni-name">University of Michigan</div>
        //     </div>
        // </div>
    );
};
