import React from "react";
import {
    CardActionArea,
    CardMedia,
    CardContent,
    Typography,
    IconButton,
    CardActions,
    Card,
    makeStyles,
    Zoom,
} from "@material-ui/core";
import {
    Share,
    AddShoppingCart,
    FavoriteBorderOutlined,
    RemoveShoppingCartRounded,
} from "@material-ui/icons";
import { Link } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { addCartItem, removeCartItem } from "../../action/userAction";

export const Course = (props) => {
    const { data } = props;
    const userInfo = useSelector((state) => state.userStatus.userInfo);
    const useStyle = makeStyles((theme) => ({
        root: {
            position: "relative",
            width: 312,
            margin: theme.spacing(2),
        },
        media: {
            height: 168,
            [theme.breakpoints.down("md")]: {
                height: 120,
            },
        },
        title: {
            marginTop: theme.spacing(6),
            height:116,
            [theme.breakpoints.down("md")]: {
                marginTop: theme.spacing(3),
            },
        },
        uni :{
            whiteSpace:'nowrap',
            textOverflow:'ellipsis',
            overflow:'hidden'
        }
    }));

    const dispatch = useDispatch();

    const addCartHandler = (courseId) => {
        dispatch(addCartItem(courseId));
    };
    const removeCartItemHandler = (id) => {
        dispatch(removeCartItem(id));
    };
    const classes = useStyle();
    return (
        <Card className={classes.root}>
            <Link style={{color:'black'}} to={`/detail/${data._id}`}>
                <CardActionArea>
                    <CardMedia className={classes.media} image={data.pic} src={data.pic} />
                    <CardContent className={classes.title}>
                        <Typography variant="h5" component="h4">
                            {data.title}
                        </Typography>
                        <Typography variant="body2" component="p" className={classes.uni}>
                            {data.university}
                        </Typography>
                    </CardContent>
                </CardActionArea>
            </Link>
            <CardActions disableSpacing>
                <IconButton>
                    <FavoriteBorderOutlined />
                </IconButton>
                <IconButton>
                    <Share />
                </IconButton>
                {userInfo ? (
                    userInfo.cart.includes(data._id) ? (
                        <Zoom in={userInfo.cart.includes(data._id)}>
                           <IconButton
                            onClick={() => {
                                removeCartItemHandler(data._id);
                            }}
                            style={{ marginLeft: "auto" }}
                        >
                            <RemoveShoppingCartRounded />
                        </IconButton> 
                        </Zoom>
                        
                    ) : (
                        <Zoom in={!userInfo.cart.includes(data._id)}>
                            <IconButton
                            onClick={() => {
                                addCartHandler(data._id);
                            }}
                            style={{ marginLeft: "auto" }}
                        >
                            <AddShoppingCart />
                        </IconButton>
                        </Zoom>
                        
                    )
                ) : (
                    ""
                )}
            </CardActions>
        </Card>

        // <div className="course">
        //     <div className="course-img">
        //         <span></span>
        //     </div>
        //     <span className="Uni-icon"></span>
        //     <div className="title">
        //         <h4>Using Databases with Python</h4>
        //         <div className="Uni-name">University of Michigan</div>
        //     </div>
        // </div>
    );
};
