import React, { useState, useEffect } from "react";
import Slider from "react-slick";
import { Course } from "./commonComponent/Course";
import { Certificate } from "./commonComponent/Cetificate";
import Axios from "axios";
import { useSelector } from "react-redux";
import { nodeApiLink } from "../config/NodeServerLink";

const NextArrow = (props) => {
    const { className, style, onClick } = props;
    return (
        <span className={className.replace("slick-next", "next-arrow")} style={style} onClick={onClick}>
            <i className="fas fa-chevron-right"></i>
        </span>
    );
};

const PrevArrow = (props) => {
    const { className, style, onClick } = props;
    return (
        <span className={className.replace("slick-prev", "prev-arrow")} style={style} onClick={onClick}>
            <i className="fas fa-chevron-left"></i>
        </span>
    );
};

export const CourseSlider= (props)=> {
    const {courses}= props
    // const [listCourse, setListCourse] = useState([])
    // const auth = useSelector(state => state.userStatus.auth)
    // const fetchCourse = async () =>{
    //     const Courses = await Axios.get(`${nodeApiLink}/api/course/get`)
    //     setListCourse(Courses.data)
    // }
    // useEffect(() => {
    //     fetchCourse()
    //     return () => {
    //         //
    //     }
    // }, [auth])
        const settings = {
            slidesToShow: 4,
            slidesToScroll: 4,
            dots: true,
            infinite: false,
            arrows: true,
            prevArrow: <PrevArrow />,
            nextArrow: <NextArrow />,
            responsive: [
                {
                    breakpoint: 992,
                    settings: {
                        slidesToShow: 3,
                        slidesToScroll: 3,
                    },
                },
                {
                    breakpoint: 768,
                    settings: {
                        slidesToShow: 2,
                        slidesToScroll: 2,
                        dots: false,
                        arrows: false,
                    },
                },
            ],
        };
        const course = (
            <Slider {...settings}>
                {
                courses && courses.map(course=>{
                    return <Course key={course._id} data={course}/>
                })}
            </Slider>
        );
        const certif = (
            <Slider {...settings}>
                <Certificate />
                <Certificate />
                <Certificate />
                <Certificate />
                <Certificate />
                <Certificate />
            </Slider>
        );
        return (
            <div className="slick-course">
                {props.type === "course" ? course : props.type === "certif" ? certif : ""}
            </div>
        );
    
}
