import React from "react";
import { Link } from "react-router-dom";

export const Subject = (props) => {
    const data = props.subject;
    return (
        <Link to={data.link}>
            <div className="subject">
                <img className="subject-img" alt={data.title} src={data.pic} />
                <div className="subject-text">
                    <div className="subject-name">{data.title}</div>
                    <span className="number-of-courses">{`${data.numOfCourse} khoá học`}</span>
                </div>
            </div>
        </Link>
    );
};
